import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import VueUtils from '@/libs/vue_utils.js' //全局方法
import Storage from 'vue-ls' //locationStorage

import 'element-ui/lib/theme-chalk/index.css'
import './assets/css/main.css'  //全局css

Vue.use(ElementUI)
Vue.use(VueUtils)
Vue.use(Storage, {
    namespace: 'vuejs__',
    name: 'ls', 
    storage: 'local'
});
Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



