<template>
    <div class="mainBox">
        <el-row style="padding: 20px;">
            <el-tabs v-model="tabPane" tab-position="top" style="height: 200px;">
                <el-tab-pane name='0' label="计算">
                    <el-form label-width="80px">
                        <el-col :span="6">
                            <el-form-item label="类型">
                                <el-select v-model="nowOrder.door.name" placeholder="请选择门类型" @change='doorChange()'>
                                    <el-option-group v-for="(boor,index) in boorInfo" :key="index" :label="boor.label">
                                        <el-option v-for="(item,index) in boor.options" :key="index" :label="item.name"
                                            :value="item.name">
                                            <span class="sltSpan01">{{ item.name }}</span>
                                            <span class="sltSpan02">{{item.price}}/平</span>
                                            <span class="sltSpan03">{{ item.send }}</span>
                                        </el-option>
                                    </el-option-group>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6" :offset="2">
                            <el-form-item label="宽">
                                <el-input v-model="nowOrder.input.width" placeholder="请输入宽度 米"
                                    @keyup.native="nowOrder.input.width=nowOrder.input.width.replace(/[^\.\d]/g,'')">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6" :offset="2">
                            <el-form-item label="高">
                                <el-input v-model="nowOrder.input.height" placeholder="请输入高度 米"
                                    @keyup.native="nowOrder.input.height=nowOrder.input.height.replace(/[^\.\d]/g,'')">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <!-- <br> -->
                        <el-col :span="6">
                            <el-form-item label="价格/平">
                                <el-input v-model="nowOrder.door.price" @input="nowOrder.door.name='自定义'"
                                    placeholder="请输入价格 已选类型则不需"
                                    @keyup.native="nowOrder.door.price=nowOrder.door.price.replace(/[^\.\d]/g,'')">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6" :offset="2">
                            <el-form-item label="运费">
                                <el-input v-model="nowOrder.input.freight" placeholder="请输入运费 选填"
                                    @keyup.native="nowOrder.input.freight=nowOrder.input.freight.replace(/[^\.\d]/g,'')">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6" :offset="2">
                            <el-form-item label="安装费">
                                <el-input v-model="nowOrder.input.install" placeholder="请输入安装费 选填"
                                    @keyup.native="nowOrder.input.install=nowOrder.input.install.replace(/[^\.\d]/g,'')">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <!-- <br> -->
                        <el-col :span="6">
                            <el-form-item label="起售平方">
                                <el-input v-model="nowOrder.door.minSell" @input="nowOrder.door.name='自定义'"
                                    placeholder="请输入起售平方 选填"
                                    @keyup.native="nowOrder.door.minSell=nowOrder.door.minSell.replace(/[^\.\d]/g,'')">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6" :offset="2">
                            <el-form-item label="提示">
                                <span class="greenFont" v-bind:class="{ redFont: errarea }">
                                    起售平方 {{nowOrder.door.minSell}} 平方米，当前 {{nowOrder.input.area}} 平方米
                                </span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6" :offset="2">
                            <el-form-item label="">
                                <el-button @click="count" size="medium" type="primary" :loading="btnLoading">
                                    计&nbsp;&nbsp;算</el-button>
                                <el-button @click="emptyInput" size="medium" type="info">清空表单<i
                                        class="el-icon-refresh el-icon--right"></i>
                                </el-button>
                            </el-form-item>
                        </el-col>
                        <el-col :span="16">
                            <el-form-item label="结果:">
                                <el-input type="textarea" v-model="nowOrder.Result" id="Result" ref="Result"
                                    :autosize="{ minRows: 4, maxRows: 6}" placeholder="结果">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6" style="padding-top: 30px;">
                            <el-form-item label="">
                                <el-button @click="copy" size="medium" type="warning" :loading="false">复制</el-button>
                                <el-button size="medium" type="text" @click="ResultModelShow=true">自定义输出结果<i
                                        class="el-icon-tickets el-icon--right"></i>
                                </el-button>
                            </el-form-item>
                        </el-col>
                        <el-col :span="16">
                            <el-form-item label="备注信息:">
                                <el-input type="textarea" v-model="nowOrder.note" :autosize="{ minRows: 1, maxRows: 6}"
                                    placeholder="输入备注信息 选填">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="">
                                <el-button size="medium" type="success" @click="orderSave">保存该条信息<i
                                        class="el-icon-download el-icon--right"></i>
                                </el-button>
                            </el-form-item>
                        </el-col>
                        <el-col>
                            <el-form-item label="">
                                <p style="color: rgb(197, 111, 111);">数字位请勿填写其他字符，系统将无法识别。请勿填写多个小数点，防止类型计算!</p>
                            </el-form-item>
                        </el-col>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane name='2' label="门管理">
                    <el-col style="position: relative;">
                        <el-col style="position: absolute;top: 0px;height: 6vh;">
                            <el-form label-width="80px">
                                <el-col :span="4">
                                    <el-form-item label="门类型">
                                        <el-select v-model="SboorType" placeholder="请选择门类型">
                                            <el-option v-for="(item,index) in boorInfo" :key="index" :label="item.label"
                                                :value="item.id">
                                            </el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="7">
                                    <el-form-item label="门类型新增" label-width="100px">
                                        <el-input placeholder="请输入类型名称" v-model="SboorTypeAdd">
                                            <el-button @click="SboorTypeAddf()" class="inputBotton" slot="append"
                                                type="primary" icon="el-icon-circle-plus-outline">新增</el-button>
                                        </el-input>
                                    </el-form-item>
                                </el-col>
                            </el-form>
                        </el-col>
                        <el-col class="boorInfoBox">
                            <div v-for="(boors,index) in boorInfo" :id="boors.id" @click="SboorType=boors.id">
                                <el-card class="card_boor" v-bind:class="{ card_boor_hover: SboorType==boors.id }">
                                    <div slot="header" class="clearfix">
                                        <span style="font-weight: bold;">
                                            <span style="color: rgb(34, 139, 209);font-weight:bolder;font-size: 20px;">#
                                            </span>
                                            {{boors.label}}</span>
                                        <el-button style="float: right; padding: 3px 0;color:red" type="text"
                                            @click="deleteBoorType(index)">删除
                                        </el-button>
                                        <el-button
                                            style="float: right; padding: 3px 0;margin-right: 50px;color:darkorange"
                                            type="text" @click="boorGoTop(index)"><i class="el-icon-upload2"></i> 置顶
                                        </el-button>
                                    </div>
                                    <div class="text item">
                                        <el-col class="boorLi" style="font-size: 15px;">
                                            <el-col :span="6">
                                                名称
                                            </el-col>
                                            <el-col :span="4">
                                                价格
                                            </el-col>
                                            <el-col :span="6">
                                                起售平方
                                            </el-col>
                                            <el-col :span="6">
                                                备注
                                            </el-col>
                                            <el-col :span="2">
                                                操作
                                            </el-col>
                                        </el-col>
                                    </div>
                                    <div v-for="(boor,boorIndex) in boors.options" class="text item">
                                        <el-col class="boorLi">
                                            <el-col :span="6">
                                                {{boor.name}}
                                            </el-col>
                                            <el-col :span="4" style="color: darkorange;">
                                                ￥{{boor.price}}
                                            </el-col>
                                            <el-col :span="6" style="color: rgb(110, 212, 27);">
                                                &nbsp;<span v-if="boor.minSell!=0">{{boor.minSell}} 平方米起售</span>
                                            </el-col>
                                            <el-col :span="6" style="color: rgb(27, 144, 212);">
                                                &nbsp;{{boor.send}}
                                            </el-col>
                                            <el-col :span="2" style="color: rgb(27, 144, 212);">
                                                <el-button style="padding: 3px 0;color:rgb(39, 131, 216)"
                                                    @click="boorChlidenEdit(index,boorIndex)" type="text">编辑
                                                </el-button>
                                                <el-button style="padding: 3px 0;color:red"
                                                    @click="boorChlidenDelete(index,boorIndex)" type="text">删除
                                                </el-button>
                                            </el-col>
                                        </el-col>
                                    </div>
                                    <div class="text item">
                                        <el-col class="boorLi" style="font-size: 15px;">
                                            <el-col :span="6">&nbsp;
                                                <el-input v-show="ifaddBoor==boors.id" v-model="addBoorModel.name"
                                                    placeholder="请输入名称"></el-input>
                                            </el-col>
                                            <el-col :span="4">&nbsp;
                                                <el-input v-show="ifaddBoor==boors.id" v-model="addBoorModel.price"
                                                    @keyup.native="addBoorModel.price=addBoorModel.price.replace(/[^\.\d]/g,'')"
                                                    placeholder="请输入价格"></el-input>
                                            </el-col>
                                            <el-col :span="6">&nbsp;
                                                <el-input v-show="ifaddBoor==boors.id" v-model="addBoorModel.minSell"
                                                    @keyup.native="addBoorModel.minSell=addBoorModel.minSell.replace(/[^\.\d]/g,'')"
                                                    placeholder="请输入起售"></el-input>
                                            </el-col>
                                            <el-col :span="6">&nbsp;
                                                <el-input v-show="ifaddBoor==boors.id" v-model="addBoorModel.send"
                                                    placeholder="请输入备注"></el-input>
                                            </el-col>
                                            <el-col :span="2">
                                                <el-button v-show="ifaddBoor!=boors.id"
                                                    style="padding: 3px 0;color:rgb(224, 154, 48)" type="text"
                                                    @click="ifaddBoor=boors.id">
                                                    添加门
                                                </el-button>
                                                <el-button v-show="ifaddBoor==boors.id" size="mini" type="success"
                                                    @click="addBoor(index)">添加</el-button>
                                                <el-button v-show="ifaddBoor==boors.id" size="mini" type="info"
                                                    @click="ifaddBoor=''">取消</el-button>
                                            </el-col>
                                        </el-col>
                                    </div>
                                </el-card>
                            </div>
                        </el-col>
                    </el-col>
                </el-tab-pane>
                <el-tab-pane name='1' label="数据管理">
                    <el-table :data="lsData.orderInfo" :default-sort="{prop: 'createTime',prop: 'input.sum'}" border
                        @selection-change="handleSelectionChange">
                        <el-table-column type="selection" width="55">
                        </el-table-column>
                        <el-table-column type="expand">
                            <template slot-scope="props">
                                <el-form label-position="left" inline class="demo-table-expand">
                                    <!-- <el-form-item label-width="100px" label="创建时间">
                                        <span>{{ props.row.createTime }}</span>
                                    </el-form-item>
                                    <el-form-item label="门类型">
                                        <span>{{ props.row.door.name }}</span>
                                    </el-form-item>
                                    <el-form-item label="价格/平">
                                        <span>{{ props.row.door.price }}</span>
                                    </el-form-item>
                                    <el-form-item label="是否赠送">
                                        <span>{{ props.row.door.send }}</span>
                                    </el-form-item>
                                    <el-form-item label="起售">
                                        <span>{{ props.row.door.price }}</span>
                                    </el-form-item>
                                    <el-form-item label="宽">
                                        <span>{{ props.row.input.width }}</span>
                                    </el-form-item>
                                    <el-form-item label="高">
                                        <span>{{ props.row.input.height }}</span>
                                    </el-form-item>
                                    <br>
                                    <el-form-item label="面积">
                                        <span>{{ props.row.input.area }}</span>
                                    </el-form-item>
                                    <el-form-item label="运费">
                                        <span>{{ props.row.input.freight }}</span>
                                    </el-form-item>
                                    <el-form-item label="安装费">
                                        <span>{{ props.row.input.install }}</span>
                                    </el-form-item>
                                    <el-form-item label="总价">
                                        <span>{{ props.row.input.sum }}</span>
                                    </el-form-item> -->
                                    <el-form-item label-width="50px" label="备注:">
                                        <span>{{ props.row.note }}</span>
                                    </el-form-item>
                                    <el-form-item label-width="50px" label="结果:">
                                        <span>{{ props.row.Result }}</span>
                                    </el-form-item>
                                </el-form>
                            </template>
                        </el-table-column>
                        <el-table-column prop="door.name" label="门名称" width="170">
                        </el-table-column>
                        <!-- <el-table-column label="价格" width="80">
                            <template slot-scope="scope">
                                <span>{{scope.row.door.price}}/ 平</span>
                            </template>
                        </el-table-column> -->
                        <el-table-column prop="input.width" label="宽" width="60">
                        </el-table-column>
                        <el-table-column prop="input.height" label="高" width="60">
                        </el-table-column>
                        <el-table-column label="面积" width="100">
                            <template slot-scope="scope">
                                <span>{{scope.row.input.area}} 平方米</span>
                            </template>
                        </el-table-column>
                        <el-table-column prop="input.freight" label="运费" width="60">
                        </el-table-column>
                        <el-table-column prop="input.install" label="安装费" width="80">
                        </el-table-column>
                        <el-table-column prop="input.sum" sortable label="总价" width="100">
                        </el-table-column>
                        <el-table-column prop="createTime" sortable label="创建时间" width="160">
                        </el-table-column>
                        <el-table-column prop="note" label="备注" width="150">
                        </el-table-column>
                        <el-table-column prop="Result" label="结果" width="410">
                        </el-table-column>
                        <el-table-column align="center" label="操作" width="160">
                            <template slot-scope="scope">
                                <el-button @click="nowOrder=scope.row,tabPane='0'" type="success" size="small">使用
                                </el-button>
                                <el-button @click="deleteOrder(scope.$index)" type="danger" size="small">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-col style="padding: 2vh;">
                        <el-button type="success" @click="exportJSON">数据导出</el-button>
                        <el-button type="warning" @click="dataOutShow=true">数据导入</el-button>
                        <el-button style="float: right;" @click="DeleteMore" type="danger">批量删除</el-button>
                    </el-col>
                </el-tab-pane>
                <el-tab-pane name='3' label="关于">
                    <el-col style="padding: 20px;" :span="20">
                        <el-card>
                            <h3><span style="color: darkorange;font-size: 23px;"># </span>Tips</h3>
                            <el-collapse accordion>
                                <el-collapse-item>
                                    <template slot="title">
                                        <span style="font-weight: bold;">计 算&nbsp;</span><i class="el-icon-warning"></i>
                                    </template>
                                    <div> 宽 * 高所得面积低于起售平方面积 将以起售平方计算，可另修改起售平方大小。</div>
                                    <div> 输入框中需填写数字的地方请勿填写其他字符和多个小数点，避免造成无法识别。 <i class="el-icon-warning"></i></div>
                                    <div> 门备注不会影响计算结果。</div>
                                </el-collapse-item>
                                <el-collapse-item>
                                    <template slot="title">
                                        <span style="font-weight: bold;"> Diy 结果模板&nbsp;</span>
                                    </template>
                                    <div> 点击复制弹出框中占位符，编辑至下方输入框，编辑自定义模板</div>
                                    <div> 占位符格式:  %&高%  </div>
                                    <div> 还原默认后将不能撤回。</div>
                                </el-collapse-item>
                                <el-collapse-item>
                                    <template slot="title">
                                        <span style="font-weight: bold;">数据管理&nbsp;</span>
                                    </template>
                                    <div> 计算结束后点击 [保存该条数据] 可将当前计算数据保存至 [数据管理]中。</div>
                                    <div> 数据管理中点击数据列表中 [使用] 可将该条数据转移到计算中，用于二次计算。 </div>
                                    <div> 数据导出 将所有数据导出为json文件。包括 (自定义结果模板、保存的计算数据、保存的门信息、用户信息)</div>
                                    <div> 数据导入 将导出的json文件导出至系统，所有数据将被替换。<i class="el-icon-warning"></i></div>
                                    <div> 清理浏览器缓存将清空所有数据，请注意备份!<i class="el-icon-warning"></i></div>
                                    <div> 还原默认 所有数据将清空!!! 请先进行数据导出以备份。 权重 ! <i class="el-icon-warning"></i></div>
                                    <el-button type="danger" size="mini" @click="cleanAll()">还原默认</el-button>
                                </el-collapse-item>
                            </el-collapse>
                        </el-card>
                    </el-col>
                </el-tab-pane>

            </el-tabs>
            </el-tabs>
        </el-row>
        <!-- //输出模板 -->
        <el-dialog title="编辑输出模板" :visible.sync="ResultModelShow" width="50%" :close-on-click-modal='false'>
            <el-col>
                <el-col class="ResultModelBody">
                    <p style="color: rgb(146, 146, 146);padding: 0;margin: 0;text-indent: 1em;">点击复制占位符
                        <input type="text" style="height: 1px;width: 1px;" v-model="toolinput" id="toolinput"
                            ref="toolinput">
                    </p>
                    <el-tooltip class="item" effect="dark" content="当前录入的高度" placement="top-start">
                        <el-tag @click="copytags('%$高%')">%$高%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前录入的宽度" placement="top-start">
                        <el-tag @click="copytags('%$宽%')" type="success">%$宽%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前面积: 高 * 宽" placement="top-start">
                        <el-tag @click="copytags('%$面积%')" type="info">%$面积%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前录入的运费" placement="top-start">
                        <el-tag @click="copytags('%$运费%')" type="warning">%$运费%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前录入的安装费" placement="top-start">
                        <el-tag @click="copytags('%$安装费%')" type="danger">%$安装费%</el-tag>
                    </el-tooltip>
                    <br>
                    <el-tooltip class="item" effect="dark" content="当前计算结果总价 面积 * 价格 + 运费 + 安装费"
                        placement="bottom-start">
                        <el-tag @click="copytags('%$总价%')">%$总价%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前门的价格" placement="bottom-start">
                        <el-tag @click="copytags('%$门价格%')" type="success">%$门价格%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前门的名字" placement="bottom-start">
                        <el-tag @click="copytags('%$门名字%')" type="info">%$门名字%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前门起售平方" placement="bottom-start">
                        <el-tag @click="copytags('%$门起售%')" type="warning">%$门起售%</el-tag>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="当前门的备注&赠送" placement="bottom-start">
                        <el-tag @click="copytags('%$门赠送%')" type="danger">%$门赠送%</el-tag>
                    </el-tooltip>
                </el-col>
                <el-col>
                    <el-form label-width="80px">
                        <el-form-item label="输出模板:">
                            <el-input type="textarea" class="ResultModelTextarea" v-model="ResultModelDilog"
                                :autosize="{ minRows: 4, maxRows: 6}" ref="ResultModelTextarea" placeholder="模板">
                            </el-input>
                            <span style="color: rgb(146, 146, 146);">例: 计算结果为: %$高% * %$宽% = %$面积% * %$门价格% + %$运费% +
                                %$安装费% = %$总价%</span>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-col>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="ResultModelShow = false">取 消</el-button>
                <el-button size="small" type="primary" @click="ResultModelDilog=ResultModelDilog.replace(/\s*/g, '')">
                    去除空格</el-button>
                <el-button style="float: left;" size="small" type="warning"
                    @click="ResultModelDilog='计算结果为:%$高%*%$宽%=%$面积%*%$门价格%+%$运费%+%$安装费%=%$总价%'">还原默认
                </el-button>
                <el-button size="small" type="success" @click="ResultModelSave">保 存
                </el-button>
            </span>
        </el-dialog>
        <!-- 数据导入 -->
        <el-dialog title="数据导入" :visible.sync="dataOutShow" width="50%" :close-on-click-modal='false'>
            <p style="margin-left: 10%;">点击选择文件 或 将文件拽至选框<span class="redFont"
                    style="float: right;margin-right: 10%;">导入将替换并覆盖当前所有数据！</span></p>
            <input id="file" class="updataInput" type="file" accept=".json" />
            <span slot="footer" class="dialog-footer">
                <el-button @click="dataOutShow = false">取 消</el-button>
                <el-button type="success" @click="importJSON">导 入</el-button>
            </span>
        </el-dialog>
        <!-- 编辑门 -->
        <el-dialog title="编辑" :visible.sync="boorChlidenEditShow" width="50%" :close-on-click-modal='false'>
            <div>
                <el-form>
                    <el-col :span="6">
                        <el-form-item label="名称">
                            <el-input v-model="addBoorModel.name" placeholder="请输入">
                            </el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="价格">
                            <el-input v-model="addBoorModel.price" placeholder="请输入"
                                @keyup.native="addBoorModel.price=addBoorModel.price.replace(/[^\.\d]/g,'')">
                            </el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="起售">
                            <el-input v-model="addBoorModel.minSell" placeholder="请输入"
                                @keyup.native="addBoorModel.minSell=addBoorModel.minSell.replace(/[^\.\d]/g,'')">
                            </el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="备注">
                            <el-input v-model="addBoorModel.send" placeholder="请输入">
                            </el-input>
                        </el-form-item>
                    </el-col>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="boorChlidenEditShow = false">取 消</el-button>
                <el-button type="success" @click="boorChlidenEditSave">保 存</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import FileSaver from 'file-saver'
    export default {
        name: "doorCount",
        data() {
            return {
                tabPane: '0',
                index: 0,
                boorIndex: 0,
                btnLoading: false,
                dataOutShow: false,
                ResultModelShow: false,
                boorChlidenEditShow: false,
                updataBoxShow: false,

                SboorType: '',
                SboorTypeAdd: '',
                ResultModel: '计算结果为:%$高%*%$宽%=%$面积%*%$门价格%+%$运费%+%$安装费%=%$总价%',
                ResultModelDilog: '计算结果为:%$高%*%$宽%=%$面积%*%$门价格%+%$运费%+%$安装费%=%$总价%',
                tableDeleteMore: [],

                ifaddBoor: '',
                addBoorModel: {
                    price: '',
                    name: '',
                    send: '',
                    minSell: '',
                },

                toolinput: '',
                errarea: '',
                //所有门
                boorInfo: [
                    {
                        id: 'u8c37u4ed3u95e8',
                        label: '谷仓门',
                        options: [
                            {
                                price: '749',
                                name: '实木复合谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            },
                            {
                                price: '849',
                                name: '贴皮谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            },
                            {
                                price: '849',
                                name: '黑板门谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            }, {
                                price: '849',
                                name: '普通玻璃谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            },
                            {
                                price: '949',
                                name: '钢化玻璃谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            },
                            {
                                price: '899',
                                name: '长虹玻璃谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            },
                            {
                                price: '949',
                                name: '镜子谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            },
                            {
                                price: '1089',
                                name: '实木谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            }, {
                                price: '689',
                                name: '铝合金单层钢化谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            }, {
                                price: '839',
                                name: '铝合金双层钢化谷仓门',
                                send: '送轨道',
                                minSell: '0',
                            },
                        ]
                    },
                    {
                        id: 'u6298u53e0u95e8u000du000a',
                        label: '折叠门',
                        options: [
                            {
                                price: '699',
                                name: '铝合金单层折叠门',
                                send: '2平方起售',
                                minSell: '2',
                            }, {
                                price: '849',
                                name: '铝合金双层折叠门',
                                send: '2平方起售',
                                minSell: '2',
                            }]
                    },
                    {
                        id: 'u63a8u62c9u95e8',
                        label: '推拉门',
                        options: [
                            {
                                price: '699',
                                name: '铝合金单层推拉门',
                                send: '单门1.2平方起',
                                minSell: '1.2',
                            },
                            {
                                price: '849',
                                name: '铝合金双层推拉门',
                                send: '单门1.2平方起',
                                minSell: '1.2',
                            }]
                    },
                    {
                        id: 'u9690u5f62u95e8',
                        label: '隐形门',
                        options: [
                            {
                                price: '2300',
                                name: '平板隐形门 含框架',
                                send: '2100*850内',
                                minSell: '0',
                            }, {
                                price: '2800',
                                name: '大线条隐形门 含框架',
                                send: '2100*850内',
                                minSell: '0',
                            },
                        ]
                    },
                    {
                        id: 'u5176u4ed6',
                        label: '其他',
                        options: [
                            {
                                price: '999',
                                name: '加磁黑板门',
                                send: '送轨道',
                                minSell: '0',
                            },
                            {
                                price: '650',
                                name: '平板木饰面',
                                send: '',
                                minSell: '0',
                            }, {
                                price: '750',
                                name: '大线条木饰面',
                                send: '',
                                minSell: '0',
                            }
                        ]
                    },
                ],
                //当前信息
                nowOrder: {
                    //宽、高、运费、安装、面积
                    input: {
                        width: '',
                        height: '',
                        freight: 0,
                        install: 0,
                        area: 0,
                        sum: 0,
                    },
                    //门
                    door: {
                        price: '',
                        name: '',
                        send: '',
                        minSell: 0
                    },
                    Result: '',
                    note: '',
                    createTime: '',
                },
                lsData: {
                    userInfo: {
                        userName: '',
                        passWord: '',
                    },
                    ResultModel: '',//输出模板
                    boorInfo: [],//所有门
                    orderInfo: [],//数据
                },
            };
        },
        mounted() {
            if (this.$ls.get('lsData')) {
                this.lsData = this.$ls.get('lsData')
                this.boorInfo = this.$ls.get('lsData').boorInfo
                this.ResultModel = this.$ls.get('lsData').ResultModel
                this.ResultModelDilog = this.$ls.get('lsData').ResultModel
            } else {
                this.lsData.ResultModel = this.ResultModel
                this.lsData.boorInfo = this.boorInfo
                this.$ls.set('lsData', this.lsData)
            }
        },
        watch: {
            SboorType(val) {
                document.getElementById(this.SboorType).scrollIntoView({ behavior: "smooth" });
            }
        },
        methods: {
            //门select改变
            doorChange() {
                for (var i = 0; i < this.boorInfo.length; i++) {
                    for (var d = 0; d < this.boorInfo[i].options.length; d++) {
                        if (this.nowOrder.door.name == this.boorInfo[i].options[d].name) {
                            this.nowOrder.door.name = this.boorInfo[i].options[d].name
                            this.nowOrder.door.price = this.boorInfo[i].options[d].price
                            this.nowOrder.door.send = this.boorInfo[i].options[d].send
                            this.nowOrder.door.minSell = this.boorInfo[i].options[d].minSell
                            return
                        }
                    }
                }
            },
            //清空表单
            emptyInput() {
                this.nowOrder.input.width = ''
                this.nowOrder.input.height = ''
                this.nowOrder.input.freight = 0,
                    this.nowOrder.input.install = 0
                this.nowOrder.door.price = ''
                this.nowOrder.door.name = ''
                this.nowOrder.door.send = ''
                this.nowOrder.door.minSell = 0
                this.$message('  已  清  空  !');
            },
            //复制
            copy() {
                let url = document.querySelector('#Result');
                url.select(); // 选择对象
                document.execCommand("Copy");
                this.$message({
                    message: '成功复制！',
                    type: 'success'
                });
                this.$refs.Result.blur()//失去焦点
            },
            //复制tags
            copytags(value) {
                this.toolinput = value
                setTimeout(() => {
                    let url = document.querySelector('#toolinput');
                    url.select(); // 选择对象
                    document.execCommand("Copy");
                }, 100);
                this.$message({
                    message: '成功复制！',
                    type: 'success'
                });
                setTimeout(() => {
                    this.$refs.ResultModelTextarea.focus()
                }, 100);
            },
            //计算
            count() {
                this.btnLoading = true
                if (this.nowOrder.input.width && this.nowOrder.input.height && this.nowOrder.door.price) {
                    this.nowOrder.input.area = ((this.nowOrder.input.width * this.nowOrder.input.height).toFixed(2)) * 1
                    this.nowOrder.door.minSell = this.nowOrder.door.minSell * 1
                    if (this.nowOrder.input.area < this.nowOrder.door.minSell) {
                        this.nowOrder.input.area = this.nowOrder.door.minSell
                        this.$message.error({
                            showClose: true,
                            duration: 8000,
                            message: '当前门平方 不足 该门起售平方，已按起售平方计算'
                        });
                        this.errarea = true
                    } else {
                        this.errarea = false
                        this.$message({
                            message: '已完成!',
                            type: 'success'
                        });
                    }
                    this.nowOrder.input.sum = ((this.nowOrder.input.area * this.nowOrder.door.price) + parseInt(Number(this.nowOrder.input.install) + Number(this.nowOrder.input.freight))).toFixed(2)
                    this.nowOrder.Result = this.ResultModelPrint(this.nowOrder)

                } else {
                    this.$message.error('宽、高、价格 不能为空!');
                }
                this.nowOrder.createTime = this.dateFormat()
                this.btnLoading = false
            },
            //输入模板
            ResultModelPrint(nowOrder) {
                var teml = this.ResultModel.split('%')
                var sum = ''
                for (var i = 0; i < teml.length; i++) {
                    switch (teml[i]) {
                        case '$高': {
                            teml[i] = nowOrder.input.height
                            continue;
                        }
                        case '$宽': {
                            teml[i] = nowOrder.input.width
                            continue;
                        }
                        case '$面积': {
                            teml[i] = nowOrder.input.area
                            continue;
                        }
                        case '$运费': {
                            teml[i] = nowOrder.input.freight
                            continue;
                        }
                        case '$安装费': {
                            teml[i] = nowOrder.input.install
                            continue;
                        }
                        case '$总价': {
                            teml[i] = nowOrder.input.sum
                            continue;
                        }
                        case '$门价格': {
                            teml[i] = nowOrder.door.price
                            continue;
                        }
                        case '$门名字': {
                            teml[i] = nowOrder.door.name
                            continue;
                        }
                        case '$门起售': {
                            teml[i] = nowOrder.door.minSell
                            continue;
                        }
                        case '$门赠送': {
                            teml[i] = nowOrder.door.send
                            continue;
                        }
                    }
                }
                for (var a = 0; a < teml.length; a++) {
                    sum = sum + teml[a]
                }
                return sum
            },
            //模板保存
            ResultModelSave() {
                this.lsData = this.$ls.get('lsData')
                this.ResultModel = this.ResultModelDilog
                this.lsData.ResultModel = this.ResultModel
                this.$ls.set('lsData', this.lsData)
                this.ResultModelShow = false
                this.$message({
                    message: '保存成功！',
                    type: 'success'
                });

            },
            //计算保存
            orderSave() {
                this.lsData = this.$ls.get('lsData')
                if (!this.nowOrder.createTime || this.nowOrder.createTime == '') {
                    this.$message.error('请勿添加空数据!');
                    return
                }
                for (var i = 0; i < this.lsData.orderInfo.length; i++) {
                    if (this.lsData.orderInfo[i].createTime == this.nowOrder.createTime) {
                        this.$message.error('该数据已存在，请勿重复添加!');
                        return;
                    }
                }
                this.lsData.orderInfo[this.lsData.orderInfo.length] = this.nowOrder
                this.$ls.set('lsData', this.lsData)
                this.$message({
                    message: '保存成功!',
                    type: 'success'
                });
                this.btnLoading = false
            },
            //删除
            deleteOrder(index) {
                this.$confirm('此操作将永久删除该条数据, 是否继续?', '提示', {
                    confirmButtonText: '删除',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.lsData.orderInfo.splice(index, 1)
                    this.$ls.set('lsData', this.lsData)
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            //handleSelectionChange
            handleSelectionChange(val) {
                this.tableDeleteMore = val
            },
            //批量删除
            DeleteMore: function () {
                if (this.tableDeleteMore.length == 0)
                    return
                this.$confirm('此操作将删除这些数据, 是否继续?', '提示', {
                    confirmButtonText: '删除',
                    cancelButtonText: '取消',
                    confirmButtonClass: 'el-button--danger',
                    type: 'warning'
                }).then(() => {
                    for (var i = 0; i < this.tableDeleteMore.length; i++) {
                        for (var t = 0; t < this.lsData.orderInfo.length; t++) {
                            if (this.tableDeleteMore[i].createTime == this.lsData.orderInfo[t].createTime) {
                                this.lsData.orderInfo.splice(t, 1)
                                continue;
                            }
                        }
                    }
                    this.$ls.set('lsData', this.lsData)
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            //新增门类型
            SboorTypeAddf() {
                if (!this.SboorTypeAdd) {
                    this.$message.error("类型名不能为空!");
                    return
                }
                for (var h = 0; h < this.boorInfo.length; h++) {
                    if (this.boorInfo[h].label == this.SboorTypeAdd) {
                        this.$message.error("[" + this.SboorTypeAdd + "] 该名字已存在,请勿重复!");
                        return
                    }
                }
                var unicode = ''
                for (var i = 0; i < this.SboorTypeAdd.length; i++) {
                    var temp = this.SboorTypeAdd.charAt(i);
                    unicode += temp.charCodeAt(0).toString(16);
                }
                this.boorInfo.push({
                    id: unicode,
                    label: this.SboorTypeAdd,
                    options: []
                })
                this.lsData.boorInfo = this.boorInfo
                this.$ls.set('lsData', this.lsData)
                setTimeout(() => {
                    this.SboorType = unicode
                }, 200);
                this.$message({
                    message: '保存成功!',
                    type: 'success'
                });
            },
            //删除门类型
            deleteBoorType(index) {
                this.$confirm('此操作将永久删除该条数据, 是否继续?', '提示', {
                    confirmButtonText: '删除',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.boorInfo.splice(index, 1)
                    this.lsData.boorInfo = this.boorInfo
                    this.$ls.set('lsData', this.lsData)
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            //置顶
            boorGoTop(index) {
                let boor = this.boorInfo[index]
                this.boorInfo.splice(index, 1)
                this.boorInfo.unshift(boor)
                setTimeout(() => {
                    this.SboorType = boor.id
                }, 200);
                this.lsData.boorInfo = this.boorInfo
                this.$ls.set('lsData', this.lsData)
                this.$message({
                    type: 'success',
                    message: '置顶成功!'
                });
            },
            //添加门
            addBoor(index) {
                let addData = {}
                if (this.addBoorModel.price && this.addBoorModel.name) {
                    addData['name'] = this.addBoorModel.name
                    addData['price'] = this.addBoorModel.price
                    addData['send'] = this.addBoorModel.send
                    addData['minSell'] = this.addBoorModel.minSell
                } else {
                    this.$message.error('价格，名称  不能为空!');
                    return
                }
                this.boorInfo[index].options.push(addData)
                this.addBoorModel.name = ''
                this.addBoorModel.price = ''
                this.addBoorModel.send = ''
                this.addBoorModel.minSell = ''
                this.ifaddBoor = ''
                this.lsData.boorInfo = this.boorInfo
                this.$ls.set('lsData', this.lsData)
                this.$message({
                    type: 'success',
                    message: '添加成功!'
                });
            },
            //删除门
            boorChlidenDelete(index, boorIndex) {
                this.$confirm('是否删除 [ ' + this.boorInfo[index].options[boorIndex].name + ' ] , 是否继续?', '提示', {
                    confirmButtonText: '删除',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.boorInfo[index].options.splice(boorIndex, 1)
                    this.lsData.boorInfo = this.boorInfo
                    this.$ls.set('lsData', this.lsData)
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            //编辑门
            boorChlidenEdit(index, boorIndex) {
                this.ifaddBoor = ''
                this.addBoorModel.name = this.boorInfo[index].options[boorIndex].name
                this.addBoorModel.price = this.boorInfo[index].options[boorIndex].price
                this.addBoorModel.send = this.boorInfo[index].options[boorIndex].send
                this.addBoorModel.minSell = this.boorInfo[index].options[boorIndex].minSell
                this.index = index
                this.boorIndex = boorIndex
                this.boorChlidenEditShow = true
            },
            //编辑门-保存
            boorChlidenEditSave() {
                var index = this.index
                var boorIndex = this.boorIndex
                this.boorInfo[index].options[boorIndex].name = this.addBoorModel.name
                this.boorInfo[index].options[boorIndex].price = this.addBoorModel.price
                this.boorInfo[index].options[boorIndex].send = this.addBoorModel.send
                this.boorInfo[index].options[boorIndex].minSell = this.addBoorModel.minSell
                this.lsData.boorInfo = this.boorInfo
                this.$ls.set('lsData', this.lsData)
                this.$message({
                    type: 'success',
                    message: '修改成功!'
                });
                this.boorChlidenEditShow = false
            },
            //还原
            cleanAll(){
                this.$confirm('是否还原默认，所有数据将清空!!!! , 是否继续?', '提示', {
                    confirmButtonText: '删除',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$ls.set('lsData', '')
                    location.reload()
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            //导入导出
            exportJSON() {
                const data = JSON.stringify(this.lsData)
                const blob = new Blob([data], { type: '' })
                FileSaver.saveAs(blob, this.dateFormat() + '.json')
                this.$message({
                    type: 'success',
                    showClose: true,
                    duration: 8000,
                    message: '数据导出成功，请等待下载完成!'
                });
            },
            importJSON() {
                var a = document.getElementById('file').files[0].name.split('.')
                if (a[1] != 'json' && a[1] != 'JSON') {
                    this.$message.error({
                        showClose: true,
                        duration: 8000,
                        message: '导入文件格式错误！！！'
                    });
                    return
                }
                const file = document.getElementById('file').files[0]
                const reader = new FileReader()
                reader.readAsText(file)
                const _this = this
                reader.onload = function () {
                    _this.lsData = JSON.parse(this.result)
                    _this.$ls.set('lsData', _this.lsData)
                    _this.dataOutShow = false
                    _this.$message({
                        type: 'success',
                        showClose: true,
                        duration: 8000,
                        message: '数据导入成功!'
                    });
                }
            }
        }
    };
</script>


