<template>
    <div class="mainBox login">
        <el-col :span="6" class="loginFormBox">
            <el-card>
                <el-col>
                    <h2 class="diyFont1" style="font-size: 30px;">T o o l <br>s y s t e m</h2>
                </el-col>
                <el-form label-width="60px" @submit.native.prevent>
                    <el-form-item label="账 号 :">
                        <el-input v-model="userName" @keyup.enter.native="userLogin" suffix-icon="el-icon-service" autofocus="true"></el-input>
                    </el-form-item>
                    <el-form-item label="密 码 :">
                        <el-input v-model="passWord" @keyup.enter.native="userLogin" suffix-icon="el-icon-view" type="password"></el-input>
                    </el-form-item>
                </el-form>
                <el-col class="buttonBox">
                    <el-button type="success" size="small" @click="userLogin()">&nbsp;登 陆 <i
                            class="el-icon-arrow-right"></i></el-button>
                    <el-button type="info" size="small" @click="Tourist">游客登陆<i class="el-icon-upload el-icon--right"></i>
                    </el-button>
                </el-col>
            </el-card>
        </el-col>

    </div>
</template>

<script>
    export default {
        name: "login",
        data() {
            return {
                userName: '',
                passWord: '',
            };
        },
        mounted() {
        },
        watch: {
        },
        methods: {
            userLogin: function () {
                if(this.userName == '' && this.passWord == ''){
                    this.$notify.error({
                        title: '账号密码不能为空',
                        message: '登陆失败，请重试!'
                    });
                    return
                }
                if (this.userName == 'zhaoqian' && this.passWord == 'zhaoqian') {
                    this.$ls.set('user','zhaoqian')
                    this.jump('layout')
                } else {
                    this.$notify.error({
                        title: '密码错误',
                        message: '登陆失败，请重试!'
                    });
                }
            },
            Tourist:function(){
                    this.$ls.set('user','')
                this.jump('layout')
                this.$message('已登陆 [游客]');
            }
        }
    };
</script>


