import Vue from 'vue'
import Router from 'vue-router'
import login from '@/views/login'
import layout from '@/views/layout'

import doorCount from '@/views/tools/doorCount'
import index2 from '@/views/tools/index2'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'login',
            component: login,
            meta: {
                keepAlive: true
            },
        },
        {
            path: '/',
            name: 'layout',
            component: layout,
            redirect: { name: 'doorCount' },
            children: [
                {
                    path:'doorCount',
                    name:'doorCount',
                    component: doorCount
                },
                {
                    path:'index2',
                    name:'index2',
                    component: index2
                },
            ]
        }
    ]
})



